@use 'base/variables' as v;
@use 'base/mixins' as m;

.bancolombia,
.header-bancolombia,
.footer-bancolombia{
    background-color: v.$fondo;
}
.bancolombia{
    h2{
        color: v.$cafe;
        font-family: v.$fuentebancolombia;
        font-weight: 200;
        text-align: center;
        margin-top: 4rem;
    }
    p{
        color: v.$morado;
        text-align: center;
        font-family: v.$fuentebancolombia;
        font-weight: 100;
    
    }
    .logob{
        width: 25rem;
    }
    .portafolib{
        background-color: v.$cafe;
        padding: 0.3rem 1.5rem;
        border-radius: 1rem;
    }
    .imgb{
        align-items: center;
        width: 30rem;
        background-color: v.$cuadro;
        border-radius: 2rem;
        margin-bottom: 8rem;
        border: 0.05rem solid v.$cafe;
        img{
            width: 20rem;
            align-items: center;
            float: left;
            margin: 4rem;
            -webkit-transition: margin 0.5s ease-out;
            
            
        }
         
        img:hover {
            margin-top: 2px;
            filter: opacity(0.9);
        }
    }
    .yo{
        @include m.grid(3, 3rem);
        padding-left: 8rem;
    }
    .familia,
    .proyeccion,
    .talentob,
    .retob{
        @include m.grid(2, 0rem);
        
        padding-left: 23rem;
    }
    .fortalezasb{
        @include m.grid(3, 0rem);
    }
    
    .contenedorb{
        margin-bottom: 3rem;
    }
}

